import {createArticles} from '@/apis/acrticle'
export default {
    name:'editor',
    middleware:['auth'],
    data(){
        return{
            article:{
                title: '',
                description:'',
                body:'',
                tagList:[]
            },
            tag:''
        }
    },
    methods:{
        async onSubmit(){
            try {
                await createArticles({article:this.article})
                this.article = {
                    title: '',
                    description:'',
                    body:'',
                    tagList:[]
                }
                this.$router.push('/')
            } catch (error) {
                
            }
        },
        addTag(){
            if(this.tag) {
                this.article.tagList.push(this.tag)
                this.tag = ''
            }
        }
    }
}
